import React from "react";

const Australia491 = () => {
  return (
    <div className="immigration">
      <div className="bg-dark immigration-top"></div>
      <div className="container mt-md-5 mt-3">
      <h1>Skilled Work Regional Provisional Visa Subclass 491</h1>
          <p>
            <strong>
              491 Skilled Work Regional Visa- Provincial
            </strong>
          </p>
          <p>
            One of the important Australia Permanent Residency pathways Skilled Regional visa (subclass 489) has been replaced by
            the newly launched Subclass 491 Skilled Work Regional (Provisional)
            Visa. This visa category has been introduced by the Australian
            Department of Home Affairs, and has commenced from 16 November 2019.
          </p>
          <p>
            This is basically a point tested visa is especially for those
            skilled workers who wish to live and work in a regional area of
            Australia. Also, with this visa you are sponsored by an Australian
            State or Territory Government or an eligible family member residing
            in a designated regional area of Australia.
          </p>
          <p>
            With this visa you can stay in Australia for up till a duration of 5
            years. Designated area of Australia is any part of the country,
            except Sydney, Brisbane, Gold Coast, Perth and Melbourne. Similar to
            Subclass 489 Skilled Regional visa the regions of Newcastle,
            Wollongong and the NSW Central Coast are regarded as designated
            regional areas.
            <br />
            &nbsp;
          </p>
          <p>
            <strong>
              How much points required for 491 Visa?
            </strong>
          </p>
          <p>
            Since subclass 491 is a points-tested visa, once you submit your EOI
            in SkillSelect you will be granted a indicative points score that
            will be based on the claims you have made, In case you can obtain 65
            points or more you will be granted an Invitation to Apply&nbsp;for
            this visa type. Even if you are invited to apply your invitation
            will state the number of points that you will be required to score
            when your application is evaluated for granting of the said visa.
            <br />
            &nbsp;
          </p>
          <p>
            <strong>
              Benefits of 491 Visa
            </strong>
          </p>
          <ul>
            <li>
              You will be allowed to stay in any regional or designated area of
              Australia for a period of 5 years.
            </li>
            <li>
              With this visa you can live, work and study in a designated
              regional area of Australia
            </li>
            <li>
              Also, you can travel to and from Australia as many times as you
              want, till the time the 491 visa is valid.
            </li>
            <li>
              You can include eligible members of your family in your visa
              application.
            </li>
            <li>
              The best part about applying via this visa pathway if you fulfill
              the eligibility requirements, you can even transition to permanent
              residency in Australia.
            </li>
            <li>
              There is priority processing granted to regional visa
              applications.
            </li>
            <li>
              Incentives are granted to newcomer immigrants for staying in these
              regional or designated areas for a longer term.
            </li>
            <li>
              Additional points are granted to 491 visa applicants.
              <br />
              &nbsp;
            </li>
          </ul>
          <p>
            <strong>
              When will you be invited to apply for 491 visa?
            </strong>
          </p>
          <p>
            You will be given preference by the Australian department of
            Immigration in the following order of hierarchy:
          </p>
          <ul>
            <li>
              First priority will be granted to candidates who have a partner
              who is skilled (in terms of work experience and educational
              qualifications) or an applicant without a partner.
            </li>
            <li>
              Second priority will be given to a prospective candidate who has a
              partner having proficiency or competency in English language.
            </li>
            <li>
              Last and final preference will be given to candidates who has a
              partner who is neither eligible for proficient English or has
              skilled partner points.
              <br />
              &nbsp;
            </li>
          </ul>
          <p>
            <strong>
              Can 491 Visa Holders lodge other Visas?
            </strong>
          </p>
          <p>
            The holders of 491 visa will not be able to able to lodge under
            certain visas till the time they have held a 491 visa and have
            fulfilled its conditions for a period of 3 years. This will include
            skilled permanent residency visas like Subclass 189 and Subclass 190, Business visas (124, 132 and 188) visas, Employer Nominated PR
            Visa (186) as well as onshore partner visa (820). The need for
            immigrants to live and work in a regional or a designated area of
            Australia is driven by the need to assist economic development and
            increase population density in sparsely occupied regional areas of
            Australia.
            <br />
            &nbsp;
          </p>
          <p>
            <strong>
              Does 491 Visa lead to Permanent Residency?
            </strong>
          </p>
          <p>
            As discussed above, the 491 visa is a five-year residency visa, but
            if you meet all its eligibility requirements you can even apply for
            permanent residency in Australia via the subclass 191 Permanent
            Residence (skilled regional) visa. But prior to taking this step you
            are required to meet the following eligibility requirements:
          </p>
          <ul>
            <li>
              As an applicant you should have lived, worked or studied in an
              Australian regional or designated area for a minimum time of 3
              years (under 491 visa subclass).
            </li>
            <li>
              In the duration of these three years you must have minimum
              earnings of $53,900.
              <br />
              &nbsp;
            </li>
          </ul>
          <p>
            <strong>
              
                What happens to current 489 visa holders and those who lodge a
                489 visa before the change?
              
            </strong>
          </p>
          <p >
            Existing 489 visa holders or those who have lodged for 489 visa
            before the date of 16<sup>th</sup> November, 2019, will have to
            continue their permanent residency pathway journey via the visa
            subclass 887.
          </p>
      </div>
    </div>
  );
};

export default Australia491;
